export function exportHead() {
  const head = [];
  head.push("Code");
  head.push("Title");
  head.push("Type");
  head.push("Required credits");
  head.push("Ordinal numbers");
  head.push("Type of knowledge");
  head.push("Available status");
  head.push("Created at");
  head.push("Updated at");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.maGN);
  row.push(data.tieuDe);
  row.push(data.loaiNhom);
  row.push(data.soTCBB);
  row.push(data.stt);
  row.push(data.idLKTT.ten);
  row.push(data.isDeleted ? "Deleted" : "Available");
  row.push(data.createdAt);
  row.push(data.updatedAt);
  return row;
}
